body {
  background-color: black;
}

.loader-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

svg.loader-svg {
  width: 800px;
  max-width: 90%;
}

path {
  opacity: 0;
  stroke: rgb(110, 10, 110);
  transition: opacity 0.2s ease, stroke 0.2s ease;
  fill: transparent;
}

path.visible {
  opacity: 1;
}

path.highlight {
  stroke: rgb(110, 10, 110); /* more vibrant violet */
}

.d1,
.d2 {
  stroke-dasharray: 500;
  stroke-dashoffset: 500;
  animation: pathanim 1.2s ease forwards;
}

.d2 {
  animation-delay: 0.3s;
}

@keyframes pathanim {
  to {
    stroke-dashoffset: 0;
  }
}

.loader-wrapper.fade-out {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.fade-in-main {
  animation: fadeInMain 0.8s ease forwards;
}

@keyframes fadeInMain {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}
