@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background-color: #540c3e;
  color: #e0e0e0;
  font-family: 'Poppins', sans-serif;
  @apply font-sans;
}
html {
  scroll-behavior: smooth;
}
@keyframes custom-pulse {
  0% {
    transform: scale(1);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.5);
    opacity: 0.2;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}

.pulse-effect {
  animation: custom-pulse 0.6s ease-out forwards;
}

@font-face {
  font-family: 'wakanda';
  src: url('./assets/fonts/Panthera.ttf') format('truetype');
  font-display: swap;
}

